<template>
  <div class="news">
    <div class="banner"></div>
    <div class="news-box">
      <h1>{{detail.title}}</h1>
      <div class="news-des">

      </div>
      <div class="new-cont"></div>


      <div class="news-read">
        <h2>相关阅读</h2>
        <ul>
          <li>111111</li>
          <li>222</li>
        </ul>
      </div>

    </div>




  </div>
</template>
<script>
  import {PopAlert, ShowLoading, HideLoading} from 'common/js/mylayer'
  import {URL_BASE} from 'common/js/public'
  import {MyPost} from 'common/js/request'
  export default {
    data(){
      return{
        detail:{}
      }
    },
    created(){

    },
    methods:{
      getNewsList() { // 初始请求
        MyPost(URL_BASE+'/index.php/api/news/getDetail',
          {
            id:''
          }).then(res => {
          console.log(res)
          if (res.return_code === 0) {
            this.detail = res.data
          }
        });

      },
    }

  }

</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
.more-case
  width 1200px
  margin  0 auto
  h3
    line-height 60px
    font-size 40px
  .box-list
    background-color #ffffff
    overflow hidden
    ul
      padding 20px 0
      li
        width 283px
        float left
        margin-right 10px
        margin-bottom 70px
        cursor pointer
        text-align left
        .title
          margin-top 20px
          font-size 22px
          line-height 28px
          color #282828
        .desc
          font-size 18px
          line-height 24px
          color #505050


</style>





